import { CHANGE_ADD_BLOCK, CHANGE_REMOVE_BLOCK } from "../../actions";
import { getCropArray } from "../../../util/getCropArray";

const arrarBlocks = [
  {
    id: 0,
    title: "Обход секретаря",
    style: "#4343ff",
    hrefVideo: "url-video",
    hrefView: "url-view"
  },
  {
    id: 1,
    title: "Знакомство с ЛПР",
    style: "#62d632",
    hrefVideo: "url-video",
    hrefView: "url-view"
  },
  {
    id: 2,
    title: "Второй разговор с ЛПР",
    style: "#ffa90d",
    hrefVideo: "url-video",
    hrefView: "url-view"
  },
  {
    id: 3,
    title: "Входящий звонок от клиента",
    style: "#19aaff",
    hrefVideo: "url-video",
    hrefView: "url-view"
  },
  {
    id: 4,
    title: "Обрабокта возражений",
    style: "#ff2a2a",
    hrefVideo: "url-video",
    hrefView: "url-view"
  }
];

export const initialState = {
  blocks: [...arrarBlocks]
};

export const displayReducers = (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_ADD_BLOCK:
      return {
        ...state,
        ...state.blocks.push(action.data)
      };

    case CHANGE_REMOVE_BLOCK:
      return {
        ...state,
        blocks: [...getCropArray(state.blocks, action.data.id)] // удалаем элемент
      };
    default:
      return state;
  }
};
