import React from "react";
import BlockComponent from "../BlockComponent";
import "./style.css";
import { BlockCreateContainer } from "../../containers/BlockCreateContainer";
import HeadComponent from "../HeadComponent";

export default function DisplayWidjetComponent(props) {
  const {
    state: { state },
    handleChangeRemoveBlock
  } = props;
  return (
    <div className="widjetComponent">
      <HeadComponent />
      <div className="dislplayWidjet">
        <div className="displayWidjet__centerBlock">
          {state.blocks.map(block => (
            <BlockComponent
              key={block.id}
              id={block.id}
              style={block.style}
              title={block.title}
              hrefVideo={block.hrefVideo}
              hrefView={block.hrefView}
              handleChangeRemoveBlock={handleChangeRemoveBlock}
            />
          ))}
          <BlockCreateContainer title={"+ Новый скрипт"} />
        </div>
      </div>
    </div>
  );
}
