import { connect } from "react-redux";
import DisplayWidjetComponent from "../../components/DisplayWidjetComponent";
import { changeAddBlock, changeRemoveBlock } from "../../store/actions";

const mapStateToProps = state => ({
  state: state
});

const mapDispatchToProps = dispatch => ({
  handleChangeAddBlock: properties => dispatch(changeAddBlock(properties)),
  handleChangeRemoveBlock: id => dispatch(changeRemoveBlock(id))
});

export const DisplayWidjetContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(DisplayWidjetComponent);
