import { connect } from "react-redux";
import {changeAddBlock} from "../../store/actions";
import BlockCreateComponent from "../../components/BlockCreateComponent";
import {getLastId} from "../../util/getLastId";

const mapStateToProps = state => ({
    lastIdBlocks: getLastId(state.state.blocks),
});

const mapDispatchToProps = dispatch => ({
    handleChangeAddBlock: properties => dispatch(changeAddBlock(properties)),
});

export const BlockCreateContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(BlockCreateComponent);
