import React, { useState } from "react";
import "./style.css";

export default function MenuCreateComponent(props) {
  const { lastIdBlocks, handleChangeAddBlock, isOpen, setIsOpen } = props;

  const [isStyleText, setIsStyleText] = useState("");
  const [isTitleText, setIsTitleText] = useState("");

  return (
    <div className="menuCreateComponent">
      <div>
        <input
          type={"text"}
          placeholder={"style"}
          value={isStyleText}
          onChange={e => setIsStyleText(e.currentTarget.value)}
        />
        <input
          type={"text"}
          placeholder={"title"}
          value={isTitleText}
          onChange={e => setIsTitleText(e.currentTarget.value)}
        />
      </div>
      <input
        type={"button"}
        value={"Добавить"}
        onClick={() => {
          isStyleText &&
            isTitleText &&
            handleChangeAddBlock({
              id: lastIdBlocks + 1,
              title: isTitleText,
              style: isStyleText
            });
          setIsOpen(!isOpen);
        }}
      />
      <input
        type={"button"}
        value={"Отмена"}
        onClick={() => {
          setIsTitleText("");
          setIsStyleText("");

          setIsOpen(!isOpen);
        }}
      />
    </div>
  );
}
