import React from "react";
import './style.css';

export default function ButtonViewComponent(props) {
  const { value, setIsOpen, isOpen } = props;
  return (
    <div className='buttonViewComponent'>
        <input
            type="button"
            value={value}
            onClick={() => {
                setIsOpen &&
                    setIsOpen(!isOpen)
             }}
        />
    </div>
  );
}
