export const CHANGE_ADD_BLOCK = "CHANGE_ADD_BLOCK";
export const CHANGE_REMOVE_BLOCK = "CHANGE_REMOVE_BLOCK";

export const changeAddBlock = properties => ({
  type: CHANGE_ADD_BLOCK,
  data: properties
});

export const changeRemoveBlock = id => ({
  type: CHANGE_REMOVE_BLOCK,
  data: id
});
