import React, { useState } from "react";

import "./style.css";

export default function BlockComponent(props) {
  const {
    title,
    style,
    id,
    hrefView,
    hrefVideo,
    handleChangeRemoveBlock
  } = props;
  const [isHover, setIsHover] = useState(false);

  return (
    <div className="test">
      <div
        className="block"
        style={{ backgroundColor: `${style}` }}
        onMouseEnter={() => {
          setIsHover(true);
        }}
        onMouseLeave={() => {
          setIsHover(false);
        }}
      >
        <span className="span-text">{title}</span>
        {isHover && (
          <div>
            <div className="menuBlockComponent">
              <div
                className="centerBlock"
                onClick={e => alert(`${e.currentTarget.value}: ${hrefVideo}`)}
              >
                <input type="button" value="" />
                <i className="fas fa-play" />
              </div>
              <input
                type="button"
                value="Просмотр"
                onClick={e => alert(`${e.currentTarget.value}: ${hrefView}`)}
              />
            </div>
            {/* <input
            type="button"
            value="удалить блок"
            onClick={() =>
              handleChangeRemoveBlock({
                id: id
              })
            }
          /> */}
          </div>
        )}
      </div>
    </div>
  );
}
